@import '../../../../u1-assets/mixins/mixins';

.sideBar {
   display: grid;
   position: relative;

   .central {
      @include display(flex, $f-der: column);
      position: relative;
      background: #fff none repeat scroll 0 0;
      border: 1px solid #ede9e9;
      border-radius: 5px;
      margin-bottom: 20px;
      padding: 20px;

      .personal {
         position: relative;
         border-bottom: 1px solid #e6ecf5;
         font-weight: 500;
         font-size: 15px;
         line-height: 15px;
         margin-bottom: 20px;
         padding-bottom: 12px;
         text-transform: capitalize;
         width: 100%;
         color: #515365;

         &:before {
            @include position(absolute, -5px, $l: -21px);
            content: "";
            height: 90%;
            width: 3px;
            background: #fa6342;
         }
      }
      .btnWrapper {
         @include display(flex, $j-content: center);
         width: 100%;
      }
   }
}


