@import '../../../../u1-assets/mixins/mixins';

.central {
   @include display(flex, $f-der: column);
   background: #fff;
   border: 1px solid #ede9e9;
   border-radius: 5px;
   max-width: 100%;
   margin-bottom: 20px;
   padding: 20px;

   .info {
      display: flex;

      .imgWrapper {
         height: 60px;
         width: 70px;

         .posterPhoto {
            background-image:url("../../../../u1-assets/images/user.jpg");
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
            height: 100%;
            width: 100%;
            border-radius: 50%;
         }
      }

      .name {
         @include display(flex, center, $f-der: column);
         width: 100%;
         padding-left: 10px;

         ins {
            display: inline-block;
            width: 90%;
            font-size: 12px;
            text-decoration: none;

            a {
               color: #fa6342;
               font-size: 13.5px;
               font-weight: 500;
            }
         }

         span {
            color: #999;
            font-size: 12px;
            text-transform: capitalize;
            width: 90%;
         }
      }
   }

   .postMeta {
      float: left;
      margin-top: 15px;
      width: 100%;
      position: relative;

      .detail {
         width: 74%;

         p {
            margin: 10px 0 16px 0;
            font-size: 14px;
            letter-spacing: 0.1px;
            color: #959ab5;
            font-weight: 500;
         }
      }
   }

   .moreInfo {
      border-top: 1px solid #ede9e9;
      float: left;
      padding: 8px 0 0;
      width: 100%;
      z-index: 0;

      ul {
         float: left;
         line-height: 27px;
         margin-bottom: 0;
         margin-top: 13px;
         padding-left: 0;
         width: 66%;

         li {
            display: inline-block;
            font-size: 13px;
            margin-right: 32px;
            vertical-align: middle;

            .like {
               position: relative;

               img {
                  width: 20px;
               }

               span {
                  color: #999999;
                  font-size: 11px;
                  left: 20px;
                  position: absolute;
                  top: -10px;
                  font-weight: 400;
               }
            }
         }
      }
   }
}
