@mixin display($display,$j-content:none,
$a-items:none,$wrap:nowrap,$f-der:none) {
   display: $display;
   justify-content: $j-content;
   align-items: $a-items;
   flex-wrap: $wrap;
   flex-direction: $f-der;
}

@mixin position($position,$t:none,$r:none, $b:none, $l:none) {
   position: $position;
   top: $t;
   right: $r;
   bottom: $b;
   left: $l;
}

@mixin padding($pad...) {
   padding: $pad;
}

@mixin text($size,$weight:none,$line:none) {
   font-size: $size;
   font-weight: $weight;
   line-height: $line;
}

@mixin container() {
   height: 100%;
   width: 80%;
   display: flex;
   align-items: center;
   flex-wrap: wrap;
   margin: 0 auto;
   @media screen and (max-width: 900px) {
      width: 80%
   }
   @media screen and (max-width: 450px) {
      width: 92%
   }
}

@mixin navStyle($transform:scaleX(0),$width:100%) {
   content: "";
   height: 3px;
   background: #f9004d;
   transition: .3s;
   bottom: 0;
   width: $width;
   transform: $transform;
   transform-origin: left;
}

