@import '../../../../u1-assets/mixins/mixins';

.newPostBox {
   .postWrapper{
      display: flex;
      width: 100%;
      position: relative;
   }

   .posterImg {
      background-image: url("../../../../u1-assets/images/user.jpg");
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      height: 45px;
      width: 45px;
      border-radius: 50%;
   }

   .input {
      display: inline-block;
      width: 90%;

      textarea {
         display: flex;
         border: none;
         width: 100%;
         border-radius: 6px 6px 0 0;
         font-family: 'Roboto';
         padding: 10px;
         resize: none;
         outline: none;
      }
   }

   .postBtn {
      background: #23d2e2 none repeat scroll 0 0;
      height: 25px;
      border: medium none;
      border-radius: 5px;
      color: #fff;
      font-weight: 500;
      font-size: 16px;
      margin-top: 11px;
      padding: 5px;
      width: 100%;
      transition: all 0.2s linear 0s;

      &:hover {
         background: #fa6342;
      }
   }
}